body.theme-twentytwentyone .site .pizza-components-block .pizza-components-nav ul li a:focus {
	outline: 0;
	text-decoration: none;
	background: #222;
	background-color: #222;
}

.pbw-builder-wrapper {
padding: 40px 80px;
position: relative;
background-color: #101010;
width: 100%;
}

.pbw-builder-wrapper .fancybox-close-small svg path {
	fill: #fff;
}
.pbw-builder-title {
	font-size: 50px;
	font-weight: 700;
	text-align: center;
	margin-bottom: 40px;
	color: #fff;
}
.pbw-builder-steps {
	background-color: #726f6f61;
	position: relative;
}


.pbw-builder-wrapper[data-cols="1"] .pbw-builder-step .pbw-builder-step__components {
	grid-template-columns: repeat(1, 1fr);
}

.pbw-builder-wrapper[data-cols="2"] .pbw-builder-step .pbw-builder-step__components {
	grid-template-columns: repeat(2, 1fr);
}

.pbw-builder-wrapper[data-cols="3"] .pbw-builder-step .pbw-builder-step__components {
	grid-template-columns: repeat(3, 1fr);
}

.pbw-builder-wrapper[data-cols="4"] .pbw-builder-step .pbw-builder-step__components {
	grid-template-columns: repeat(4, 1fr);
}

.pbw-builder-wrapper[data-cols="5"] .pbw-builder-step .pbw-builder-step__components {
	grid-template-columns: repeat(5, 1fr);
}

.pbw-builder-wrapper[data-cols="6"] .pbw-builder-step .pbw-builder-step__components {
	grid-template-columns: repeat(6, 1fr);
}

.pbw-builder-wrapper[data-cols="7"] .pbw-builder-step .pbw-builder-step__components {
	grid-template-columns: repeat(7, 1fr);
}

.pbw-builder-wrapper[data-cols="8"] .pbw-builder-step .pbw-builder-step__components {
	grid-template-columns: repeat(8, 1fr);
}

.pbw-builder-step {

	display: none;

	&.active {

		display: block;
	}

	&__header {
	background-color: #e3d3d347;

	padding: 20px 26px;

		span {
			color: #fff;
			font-size: 22px;
			font-weight: 700;
		}
	}

	&__components {
		display: grid;
		grid-template-columns: repeat(7, 1fr);
		grid-row-gap: 10px;
		padding: 25px 10px;
		
	}

	&__component {
		cursor: pointer;
		padding: 0px 10px;
		border-right: 1px dashed rgb(110, 107, 107);
		.pizza-tippy {

		}

		
	}

	&__component.active {
		.pbw-builder-step__inner {
			background: radial-gradient(50% 50% at 50% 50%, #444444 0%, #222222 100%);
			border: 1px solid #FFFFFF;
		}
		.pbw-builder-step__name {
			color: #FF0329;
		}
		svg path {
			fill: #fff;
		}
	}

	&__inner {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		position: relative;
		padding: 10px 0;
		
		height: 100%;
	}

	&__name {
		font-size: 14px;
		color: #fff;
		font-weight: 700;
		text-transform: uppercase;
		margin-bottom: 10px;
	}

	&__image {
		img {
			width: 100%;
			display: block;
			max-width: 110px;
		}
		margin-bottom: 10px;
	}

	&__price {
		color: #fff;
		font-size: 14px;
		font-weight: 700;
		margin-top: auto;
	}
}

.pbw-prev {
	position: absolute;
	left: -60px;
	top: 0;
	outline:0;
	border:0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #585858;
	height: 40px;
	width: 40px;
	cursor: pointer;
	transition: all 0.3s ease;
	padding: 0;
svg {
	transform: rotate(-180deg);
}
}
.pbw-prev:hover, .pbw-next:hover {
	background-color: #d10a28;
}
.pbw-next {
	position: absolute;
	right:-60px;
	top:0;
	outline:0;
	border:0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #FF0329;
	height: 40px;
	width: 40px;
	cursor: pointer;
	transition: all 0.3s ease;
	padding: 0;
}
.pbw-next:disabled {
	opacity: 0.5;
}
.pbw-next:disabled:hover {
	background-color: #FF0329;
}
.pbw-builder-step__choosen {
	margin-top: 60px;
	background-color: #726f6f61;
	.pbw-builder-step__header {
		display: flex;
		align-items: center;
		span:nth-of-type(2) {
			margin-left: auto;
			margin-right: 50px;
		}
	}
}
.pbw-place-order {
	outline: 0;
	border: 0;
	padding: 0 30px;
	height: 41px;
	font-size: 18px;
	font-weight: 700;
	background-color: #FF0329;
	color: #fff;
	cursor: pointer;
	transition: all 0.3s ease;
}
.pbw-place-order:hover {
	background-color: #c90f2b;
}
.pbw-place-order:disabled {

	background-color: #939393;

}
.pbw-place-order:disabled:hover {
	background-color: #939393;
}
.pbw-success-container {
	display: none;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 40px 80px;
	background-color: #525252;
	box-shadow: 6px 10px 145px;
}
.pbw-success-container svg {
	margin-bottom: 20px;
}
.pbw-success-container p {
	font-size: 22px;
	font-weight: 700;
	margin: 0;
	color: #fff;
}
.pbw-success-container.active {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	
}
.pbw-builder-button button {
	background-color: #eeeeee;
    border-color: #eeeeee;
    color: #333333;
	padding: 10px 20px;
	cursor: pointer;
	border: 0;
	transition: all 0.3s ease;
	font-weight: 600;
	display: inline-block;
}
.pbw-builder-button button:hover {
	background-color: #c5c5c5;
}
.pbw-place-order.loading:after {
	content: "";
	display: inline-block;
	width: 22px;
	height: 22px;
	border: 1px solid #BBB;
	border-left-color: #000;
	border-radius: 50%;
	margin-left: 10px;
	vertical-align: middle;
	animation: pbw-rotate 450ms infinite linear;
	visibility: visible;
	opacity: 1;
	transition: opacity .2s ease, visibility .2s ease;
}
.pbw-builder-step__components.swiper {
	padding-bottom: 30px;
	.swiper-pagination {
		bottom: 0;
	}
}
@media(max-width: 780px) {
	.pbw-builder-wrapper {
		padding: 40px 0px;
	}
	.pbw-builder-step__components {
		display: block;
	}
	.pbw-builder-step__components .slick-track {
		display: flex;
		justify-content: space-between;
		// width: 100% !important;
		margin-left: 0;
		margin-right: 0;
	}
	.pbw-builder-step__component {
		
	}
	.pbw-builder-step__choosen .pbw-builder-step__components {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-row-gap: 10px;
	}
	.pbw-builder-step__header span {
		font-size: 18px;
	}
	.pbw-builder-step__header span span {
		font-size: 22px;
	}
	.pbw-builder-step__choosen .pbw-builder-step__header span:nth-of-type(2) {
		margin-right: 10px;
	}
	.pbw-place-order {
		width: 100%;
	}
	.pbw-builder-step__name {
		font-size: 10px;
	}
	.pbw-builder-step__header {
		display: flex;
		align-items: center;
	}
	.pbw-builder-step__header .pbw-next, .pbw-builder-step__header .pbw-prev {
		position: relative;
		right: unset;
		left: unset;
	}
	.pbw-builder-step__header .pbw-next  {
		margin-left: auto;
	}
	.pbw-builder-step__header .pbw-prev {
		margin-right: 20px;
	}

	.pbw-builder-wrapper[data-cols] .pbw-builder-step.pbw-builder-step__choosen .pbw-builder-step__components {
		
		grid-template-columns: repeat(4, 1fr);
		
	}
}
@media(max-width: 576px) {
	.pbw-builder-wrapper[data-cols] .pbw-builder-step.pbw-builder-step__choosen .pbw-builder-step__components {
		
		grid-template-columns: repeat(2, 1fr);
		
	}
}
@media(max-width: 380px) {

}
@keyframes pbw-rotate {
	100% {
	transform: rotate(360deg); 
		}
			
}
